"use client";

/**
 * üéì Student Records Page
 * Demonstrates AFUED Table Component usage with all interactive features enabled.
 * Layout: Inside dashboard card with full theme compliance.
 */

import React, { useEffect } from "react";
import { Table } from "@/components/ui/Table";
import { Badge } from "@/components/ui/Badge";
import { usePage } from "@/hooks/usePage";

export default function StudentRecordsPage() {
  const { setPage } = usePage();

  useEffect(() => {
    setPage("Student Records");
  }, [setPage]);

  // üß© Mock student data
  const students = [
    {
      id: "STU001",
      name: "Aloye Breakthrough",
      department: "Computer Science",
      level: "400",
      status: "Active",
      cgpa: 4.25,
    },
    {
      id: "STU002",
      name: "Muna Love",
      department: "Mathematics",
      level: "300",
      status: "Probation",
      cgpa: 1.85,
    },
    {
      id: "STU003",
      name: "Joshua Okafor",
      department: "Biology",
      level: "200",
      status: "Inactive",
      cgpa: 0.0,
    },
  ];

  // üß± Table Columns Definition
  const columns = [
    { header: "Student ID", accessorKey: "id" },
    { header: "Name", accessorKey: "name" },
    { header: "Department", accessorKey: "department" },
    { header: "Level", accessorKey: "level" },
    {
      header: "Status",
      accessorKey: "status",
      cell: ({ row }: any) => {
        const status = row.original.status;
        const variant =
          status === "Active"
            ? "success"
            : status === "Probation"
            ? "warning"
            : "error";
        return <Badge variant={variant}>{status}</Badge>;
      },
    },
    { header: "CGPA", accessorKey: "cgpa" },
  ];

  // üß† Handlers for row actions
  const handleView = (row: any) => alert(`Viewing ${row.name}`);
  const handleEdit = (row: any) => alert(`Editing ${row.name}`);
  const handleDelete = (row: any) => alert(`Deleting ${row.name}`);

  return (
    <div className="max-w-6xl mx-auto p-6">
      <div className="bg-surface rounded-xl shadow-md p-4">
        <h2 className="text-2xl font-semibold mb-4 text-textPrimary">
          Student Records
        </h2>

        <Table
          columns={columns}
          data={students}
          enableSearch
          enableSort
          enablePagination
          enableExport
          enableActions
          onView={handleView}
          onEdit={handleEdit}
          onDelete={handleDelete}
        />
      </div>
    </div>
  );
}
