/**
 * üìä AFUED Table Component (Extended)
 * Consumes: colors.surface, colors.border, text.textPrimary, text.textOnPrimary
 * Description: Universal, reusable data table with optional search, sort, filter, pagination, export, and actions.
 */

"use client";

import React, { useState } from "react";
import {
  useReactTable,
  getCoreRowModel,
  getSortedRowModel,
  getFilteredRowModel,
  getPaginationRowModel,
  flexRender,
} from "@tanstack/react-table";
import { Input } from "@/components/ui/Input";
import { Button } from "@/components/ui/Button";
import { Download, Edit, Eye, Trash } from "lucide-react";
import theme from "@/styles/theme";

interface TableProps<TData extends object> {
  columns: any[];
  data: TData[];
  enableSearch?: boolean;
  enableSort?: boolean;
  enablePagination?: boolean;
  enableFilter?: boolean;
  enableExport?: boolean;
  enableActions?: boolean;
  pageSize?: number;
  onEdit?: (row: TData) => void;
  onView?: (row: TData) => void;
  onDelete?: (row: TData) => void;
}

export function Table<TData extends object>({
  columns,
  data,
  enableSearch = true,
  enableSort = true,
  enablePagination = true,
  enableFilter = false,
  enableExport = false,
  enableActions = false,
  pageSize = 10,
  onEdit,
  onView,
  onDelete,
}: TableProps<TData>) {
  const [globalFilter, setGlobalFilter] = useState("");

  const table = useReactTable({
    data,
    columns: enableActions
      ? [
          ...columns,
          {
            header: "Actions",
            id: "actions",
            cell: ({ row }: any) => (
              <div className="flex gap-2">
                {onView && (
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => onView(row.original)}
                  >
                    <Eye size={16} />
                  </Button>
                )}
                {onEdit && (
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => onEdit(row.original)}
                  >
                    <Edit size={16} />
                  </Button>
                )}
                {onDelete && (
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => onDelete(row.original)}
                  >
                    <Trash size={16} className="text-error" />
                  </Button>
                )}
              </div>
            ),
          },
        ]
      : columns,
    state: { globalFilter },
    onGlobalFilterChange: setGlobalFilter,
    getCoreRowModel: getCoreRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
    getSortedRowModel: enableSort ? getSortedRowModel() : undefined,
    getPaginationRowModel: enablePagination ? getPaginationRowModel() : undefined,
  });

  const exportCSV = () => {
    const headers = table
      .getAllColumns()
      .filter((col) => col.id !== "actions")
      .map((col) => col.id);
    const csvRows = [
      headers.join(","),
      ...data.map((row) =>
        headers.map((key) => (row as any)[key]).join(",")
      ),
    ];
    const blob = new Blob([csvRows.join("\n")], { type: "text/csv" });
    const link = document.createElement("a");
    link.href = URL.createObjectURL(blob);
    link.download = "table-data.csv";
    link.click();
  };

  return (
    <div className="w-full p-4 bg-surface rounded-xl shadow-md">
      {/* üîç Search & Export Controls */}
      {(enableSearch || enableExport) && (
        <div className="flex flex-wrap justify-between items-center mb-4 gap-2">
          {enableSearch && (
            <Input
              placeholder="Search..."
              value={globalFilter ?? ""}
              onChange={(e) => setGlobalFilter(e.target.value)}
              className="max-w-xs"
            />
          )}
          {enableExport && (
            <Button variant="secondary" onClick={exportCSV}>
              <Download size={16} className="mr-2" /> Export CSV
            </Button>
          )}
        </div>
      )}

      {/* üßæ Table */}
      <div className="overflow-x-auto">
        <table className="min-w-full text-sm text-left border border-border">
          <thead className="bg-surfaceElevated text-textPrimary">
            {table.getHeaderGroups().map((headerGroup) => (
              <tr key={headerGroup.id}>
                {headerGroup.headers.map((header) => (
                  <th
                    key={header.id}
                    className="px-4 py-2 border-b border-border font-semibold select-none cursor-pointer"
                    onClick={enableSort ? header.column.getToggleSortingHandler() : undefined}
                  >
                    {header.isPlaceholder
                      ? null
                      : flexRender(
                          header.column.columnDef.header,
                          header.getContext()
                        )}
                  </th>
                ))}
              </tr>
            ))}
          </thead>

          <tbody>
            {table.getRowModel().rows.map((row) => (
              <tr key={row.id} className="hover:bg-surfaceElevated transition-all">
                {row.getVisibleCells().map((cell) => (
                  <td key={cell.id} className="px-4 py-2 border-b border-border">
                    {flexRender(cell.column.columnDef.cell, cell.getContext())}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* üìÑ Pagination */}
      {enablePagination && (
        <div className="flex justify-between items-center mt-4">
          <Button
            variant="secondary"
            onClick={() => table.previousPage()}
            disabled={!table.getCanPreviousPage()}
          >
            Previous
          </Button>
          <span className="text-sm text-textSecondary">
            Page {table.getState().pagination.pageIndex + 1} of{" "}
            {table.getPageCount()}
          </span>
          <Button
            variant="secondary"
            onClick={() => table.nextPage()}
            disabled={!table.getCanNextPage()}
          >
            Next
          </Button>
        </div>
      )}
    </div>
  );
}
